@media (min-width:786px) {
  .form {
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    align-items: center;
  }
  .input {
    margin-bottom: 0;
  }
}

html {
  font-size: 10px;
  margin:0;
}

* {
  margin: 0
}

body {
  margin: 0;
  padding: 0;
  background: linear-gradient(45deg, #0d253f, #01b4e4, #90cea1);
  background-repeat: no-repeat;
  width: 100%;
  min-height: 100vh;
}

h1 {
  color: linear-gradient(45deg, #0d253f, #01b4e4, #90cea1);
  border-radius: 50px;
}

p {
  font-size: 1.2rem;
}

small {
  font-size: 1.2rem;
}

.container {
  margin: 0;
}

.button {
  margin: 5px;
}

.title {
  font-size: 4.4rem;
  text-align: center
}

.form {
  display: grid;
}

.label {
  font-size: 1.2rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
}

.input {
  font-size: 1.6rem;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 20px;
  border: 1px solid #ddd;
  margin-bottom: 1rem;
}



table {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 400px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

table thead tr {
  background-color: #009879;
  color: #ffffff;
  text-align: left;
}

table th, table td {
  padding: 12px 15px;
}

table tbody tr {
  border-bottom: 1px solid #dddddd;
}

table tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

table tbody tr:last-of-type {
  border-bottom: 2px solid #009879;
}

.poster_img {
  border-radius: 25px;
  width:185px;
  height:278px;
}

.rating_circle{
  font-size: 12px;
}


